import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';

@IonicPage()
@Component({
  selector: 'page-new-list',
  templateUrl: 'new-list.html',
})
export class NewListPage {

  ///标题名称
  titlename: string = "在线新闻";

  ///数据的列表
  datalist: Array<any> = [];

  ///数据连接
  dataurl: string = "News/GetNewsStatusList";

  ///id的列信息
  idcol: string = "CODE";

  ///搜索的内容
  searchQuery: string = "";

  ///
  listType: string = "unread";

  ///数据的参数信息
  dataParam: any = {};

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
  ) {

  }

  ionViewCanEnter() {
    this._flush();
  }

  changePage() {
    this._flush();
  }

  private _flush(): void {
    var stateParam: any = {};
    stateParam["status"] = this._getStateInfo();
    this.dataParam = stateParam;
  }

  /**
   * 获取当前单项列表状态
   *
   * @private
   * @returns {string}
   * @memberof STaskListPage
   */
  private _getStateInfo(): string {
    var result: string = "未读";
    if (this.listType == "unread") {
      result = "未读";
    } else if (this.listType == "read") {
      result = "已读";
    } else {
      result = "";
    }
    return result;
  }

  newDetail(item) {
    this.navCtrl.push("NewDetailPage", { item });
  }

}



